import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

class SimpleSlider extends React.Component {
  render() {
    var settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };

    const { images } = this.props;
    return (
      <Slider {...settings}>
        <div>
          {images.map((item, i) => {
            return (
              <div
                key={i}
                style={{
                  width: "100%",
                  height: "450px",
                  background: `url(${item.url}) center/cover no-repeat`
                }}
              ></div>
            );
          })}
        </div>
        <div>
          <h3>2</h3>
        </div>
        <div>
          <h3>3</h3>
        </div>
        <div>
          <h3>4</h3>
        </div>
        <div>
          <h3>5</h3>
        </div>
        <div>
          <h3>6</h3>
        </div>
      </Slider>
    );
  }
}

export default SimpleSlider;
{
  <Slider {...settings}>
    {images.map((item, i) => {
      return (
        <div key={i}>
          <div
            style={{
              width: "100%",
              height: "450px",
              background: `url(${item.url}) center/cover no-repeat`
            }}
          ></div>
        </div>
      );
    })}
  </Slider>;
}
